@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@400;800&family=Roboto:wght@300&display=swap);

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
/* global color  */

:root {
  --primary: #a6ffff;
  --secondary: #009985;
  --black: rgb(32, 32, 32);
  --bg_color: #0d2436;
  --font_color: #ffffff;
  --font_color_hover: #cccccc;
  --card_bg: silver;
  --black2: black;
}

/* global button  */

* {
  margin: 0;
  padding: 0;
  background-color: #0d2436;
  color: white;
  font-family: "Roboto", sans-serif;
}
/* App */
.App {
  margin: 16px 150px;
}

.categories h1 {
  font-size: 16px;
  height: 20px;
  background-color: gray;
  color: white;
  line-height: 20px;
  padding: 5px 7px;
}

.box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  grid-gap: 2px;
  gap: 2px;
}
.selected {
  background-color: #009b86 !important;
}
.profileImg {
  width: 75px;
  height: 75px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  object-fit: cover;
}
.box p {
  background-color: transparent;
}
.box .button {
  padding: 5px;
  cursor: pointer;
  background-color: #009b86;
  border: none;
  border-radius: 5px;
  outline: none;
}
.box .button:hover {
  background-color: #34ac9c;
  color: #cccccc;
}



.header {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 70px;
  font-weight: bold;
}

.submit {
  background-color: #34ac9c;
  color: white;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
  border: none;
  outline: none;
}
.submit:hover {
  background-color: #34ac9c;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.modalContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 50%;
  grid-gap: 5px;
  gap: 5px;
  position: relative;
  width: 100%;
  padding: 32px;
  max-width: 640px;
  background-color: white;
  overflow-y: scroll;
}
.modalContainer span {
  background-color: white;
  color: black;
}
.modalContainer .closeBtn {
  position: absolute;
  top: 16px;
  right: 16px;
  background-color: red;
  color: white;
  border: 1px solid white;
  outline: none;
  border-radius: 5px;
  padding: 5px;
  cursor: pointer;
}

